.popup {
  display: none;
  padding: rem(20);

  &.open {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 51;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &.success {
    .popup__block {
      grid-template-columns: 1fr;
    }

    .popup__image,
    .popup__title {
      display: none;
    }

    .success__block {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .contacts__form-content {
      display: none;
    }

    .success__block-title {
      max-width: rem(285);
    }
  }

  &__wrapper {
    background: rgba(0, 0, 0, 0.50);
    backdrop-filter: blur(7.5px);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
  }

  &__container {}

  &__block {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr;
    background: #111126;
    border-radius: 20px;
    overflow: hidden;
    position: relative;

    @include media-mx(992) {
      grid-template-columns: 1fr;
    }
  }

  &__image {
    width: 100%;
    height: 100%;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__content {
    padding: rem(60);
    height: 100%;

    @include media-mx(992) {
      padding: rem(60) rem(20);
    }
  }

  &__close {
    position: absolute;
    top: rem(8);
    right: rem(8);
    cursor: pointer;

    &:before {
      content: "";
      position: absolute;
      top: rem(-50);
      right: rem(-50);
      background: #2F2F44;
      width: rem(86);
      height: rem(86);
      border-radius: 50%;
      display: flex;
      z-index: 0
    }

    svg {
      position: relative;
      z-index: 1;
      width: rem(16);
    }
  }

  &__title {
    font-size: 36px;
    font-style: normal;
    font-weight: 200;
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;

    @include media-mx(992) {}
  }

  &__form {
    margin-top: rem(40);

    .contacts__form-button {
      &::before {
        z-index: 0;

      }

      &::after {
        z-index: 1;
      }

      span {
        position: relative;
        z-index: 2;
        transition: all 0.5s;
      }

      &:hover {
        span {
          -webkit-text-fill-color: #fff;
        }
      }
    }

    .contacts__form-file-hidden {
      display: none;


    }



    .contacts__form-file {
      border-radius: 10px;
      border: 1px dashed #D9D9D9;
      background: linear-gradient(90deg, #8F52F3 3.43%, #00AAE1 97.98%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      font-family: Oxanium;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 1;
      align-items: center;
      display: flex;
      justify-content: center;
      gap: rem(7);
      padding: rem(17);
      cursor: pointer;
      position: relative;

      &::before {
        content: "";
        background: linear-gradient(90deg, #8F52F3 3.43%, #00AAE1 97.98%);
        opacity: 0;
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        right: 0;
        z-index: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        transition: all 0.5s;
      }

      &:hover {
        &::before {
          opacity: 0.2;
        }
      }

      svg {}

      span {
        position: relative;
        color: #00AAE1;
        -webkit-text-fill-color: #00AAE1;

        &::before {
          content: "";
          position: absolute;
          bottom: 0px;
          width: 100%;
          height: 1px;
          background: #00AAE1;

        }
      }
    }
  }
}